import { defineCustomElement } from 'vue'
import Login from './components/Login.govue'
customElements.define('go-login',defineCustomElement(Login));
import Main from './components/Main.govue'
customElements.define('go-main',defineCustomElement(Main));
export const pages={
    "login":'go-login',
    "main":'go-main'
};
function gComponent(value){
    return {
        template:`<${value}></${value}>`
    }
}
const comp={};
const comps=Object.keys(pages);
comps.forEach(path=>{
    comp[path]=gComponent(pages[path]);
});
export const components=comp;