import { createApp } from 'vue'
import router from './router'
import cloud from './cloud.vue'
import axios from 'axios'
import goto from './assets/tools/goto'
import regFunc from '../index/regCustomElement'
// import {Button,Input} from '@varlet/ui'
import './assets/css/config.css'
import 'xy-ui';
// import '@varlet/ui/es/style.js'
regFunc();
const app = createApp(cloud).use(router)
// .use(Button).use(Input);
app.provide("goto", goto)
    .provide("axios", axios)
    .mixin({ inject: ["axios", "goto"] })
app.config.compilerOptions.isCustomElement = tag => tag.startsWith('go-') || tag.startsWith('xy') || tag.startsWith('common') || tag.startsWith('phone') || tag.startsWith('window');
app.mount("#cloud");
