import {createRouter,createWebHistory} from "vue-router";
import {pages} from './cloud.config.js'
import Root from './components/Root.vue'
const routes = [
  {
    path: "/:name",
    name: "routes",
    component:Root,
    beforeEnter: (to, from, next) => {
      if (to.params.name in pages) {
        next();
      } else {
        // next('/login');
        next();
        //进行路由拦截，默认跳转到service路由
      }
    },
  },
  {
    path: '/',
    component:Root,
    name:'r',
    beforeEnter: (to, from, next) => {
      next('/login');
    }
  }
];

const router =createRouter({
  history: createWebHistory("/cloud.html"),
  routes,
});

export default router;
